:root {
    --primary: #f7f07f;
    --foreground: #111283;
}
body {
    margin: 0;
    text-align: center;
    padding: 0;
    font-family: sans-serif;
    font-size: 20px;
    color: #212121;
    background-color: #f7f07f;
    background-color: var(--primary);
    font-family: -apple-system-body, BlinkMacSystemFont, SFUI, HelveticaNeue, Helvetica, Arial, sans-serif;
}
a {
    color: #f7f07f;
    color: var(--primary);
    text-decoration: none;
    border-bottom: 1px dashed #1113837a;
}

a:hover {
    border-bottom-style: solid;
}

p {
    font-size: 0.8em;
    line-height: 1.9;
    margin-bottom: 1.4em;
}

small {
    font-size: 0.6em;
    opacity: 0.5;
}

h1 {
    margin-top: 0;
    font-weight: 500;
}

h2 {
    font-size: 1em;
    font-weight: 500;
}

pre {
    width: 150%;
    margin-left: -25%;
    font-size: 1em;
}

pre,
code {
    color: #17d382;
}

@media (max-width: 850px) {
    pre {
        width: 100%;
        overflow-x: scroll;
        margin-left: 0;
    }
}

.Header {
    color: var(--foreground);
    background: var(--primary);
    filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#ff007f', endColorstr='#e5006e',GradientType=1 ); /* IE6-9 fallback on horizontal gradient */
    padding-top: 60px;
}

.Header h2 {
    opacity: 0.9;
    font-weight: normal;
    margin-bottom: 3em;
    line-height: 1.5;
}
.Header-inner {
    margin-left: auto;
    margin-right: auto;
    max-width: 450px;
    padding: 0 30px;
    text-align: left;
}

.Header-subheader {
    background: var(--foreground);
    padding: 20px 25%;
}

.Header-subheader a {
    font-size: .8em;
}

.Header-subheader .Header-inner {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    -webkit-align-items: center;
            align-items: center;
}

@media (max-width: 850px) {
    .Header-inner {
        max-width: 500px;
        text-align: center;
    }
    .Header-subheader {
        padding: 20px 5%;
    }
    .Header-subheader .Header-inner {
        -webkit-flex-wrap: wrap;
                flex-wrap: wrap;
        -webkit-justify-content: center;
                justify-content: center;
    }
}


.Example-container {
    border: 1px solid var(--foreground);
    max-width: 320px;
    margin: 0 auto;
    border-radius: 10px;
    overflow: hidden;
}
.RddItem-container {
    position: relative;
    -webkit-transition: height 600ms cubic-bezier(0.7, 0, 0.3, 1);
    transition: height 600ms cubic-bezier(0.7, 0, 0.3, 1);
}
.RddItem-itemsList {
    list-style-type: none;
    padding: 0;
    position: absolute;
    top: 0;
    left: 0;
    height: 100%;
    width: 100%;
    overflow: hidden;
    margin: 0;
}
.RddItem-itemsList li {
    -webkit-animation-duration: 600ms;
            animation-duration: 600ms;
    -webkit-animation-fill-mode: both;
            animation-fill-mode: both;
    -webkit-animation-timing-function: cubic-bezier(0.7, 0, 0.3, 1);
            animation-timing-function: cubic-bezier(0.7, 0, 0.3, 1);
    margin: 0;
}
.RddItem-animateOutToRight {
    pointer-events: none;
}
.RddItem-animateOutToRight li {
    -webkit-animation-name: outToRight;
            animation-name: outToRight;
}
.RddItem-animateOutToLeft {
    pointer-events: none;
}
.RddItem-animateOutToLeft li {
    -webkit-animation-name: outToLeft;
            animation-name: outToLeft;
}
.RddItem-animateInFromLeft li {
    -webkit-animation-name: inFromLeft;
            animation-name: inFromLeft;
}
.RddItem-animateInFromRight li {
    -webkit-animation-name: inFromRight;
            animation-name: inFromRight;
}
.RddItem-item {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    padding: 10px;
}
.RddItem-item:hover {
    text-decoration: none;
    background-color: var(--foreground);
    color: var(--primary);
}
.RddItem-button {
    background-color: var(--primary);
    cursor: pointer;
    color: var(--foreground);
    position: relative;
    width: 100%;
    border: none;
    font-size: 15px;
    line-height: 20px;
}
.RddItem-button:after {
    color: var(--foreground);
    content: '\2794';
    position: absolute;
    right: 12px;
    -webkit-transition: right 150ms ease;
    transition: right 150ms ease;
}
.RddItem-button:hover:after {
    color: var(--primary);
    right: 10px;
}

.Rdd-Header-container {
    font-size: 13px;
    background-color: var(--primary);
    border-bottom: lightgrey;
    padding: 10px;
    color: #64422b;
}

.Rdd-Header-backButton {
    color: #64422b;
    margin-right: 10px;
    font-size: 13px;
    background: none;
    border: 0;
    padding: 0;
}

.Rdd-Header-backButton:before {
    content: '\2190   ';
}

@-webkit-keyframes outToRight {
    to {
        opacity: 0;
        -webkit-transform: translate3d(80%, 0, 0);
        transform: translate3d(80%, 0, 0);
    }
}
@keyframes outToRight {
    to {
        opacity: 0;
        -webkit-transform: translate3d(80%, 0, 0);
        transform: translate3d(80%, 0, 0);
    }
}
@-webkit-keyframes outToLeft {
    to {
        opacity: 0;
        -webkit-transform: translate3d(-80%, 0, 0);
        transform: translate3d(-80%, 0, 0);
    }
}
@keyframes outToLeft {
    to {
        opacity: 0;
        -webkit-transform: translate3d(-80%, 0, 0);
        transform: translate3d(-80%, 0, 0);
    }
}
@-webkit-keyframes inFromLeft {
    from {
        opacity: 0;
        -webkit-transform: translate3d(-80%, 0, 0);
        transform: translate3d(-80%, 0, 0);
    }
    to {
        opacity: 1;
        -webkit-transform: translate3d(0, 0, 0);
        transform: translate3d(0, 0, 0);
    }
}
@keyframes inFromLeft {
    from {
        opacity: 0;
        -webkit-transform: translate3d(-80%, 0, 0);
        transform: translate3d(-80%, 0, 0);
    }
    to {
        opacity: 1;
        -webkit-transform: translate3d(0, 0, 0);
        transform: translate3d(0, 0, 0);
    }
}
@-webkit-keyframes inFromRight {
    from {
        opacity: 0;
        -webkit-transform: translate3d(80%, 0, 0);
        transform: translate3d(80%, 0, 0);
    }
    to {
        opacity: 1;
        -webkit-transform: translate3d(0, 0, 0);
        transform: translate3d(0, 0, 0);
    }
}
@keyframes inFromRight {
    from {
        opacity: 0;
        -webkit-transform: translate3d(80%, 0, 0);
        transform: translate3d(80%, 0, 0);
    }
    to {
        opacity: 1;
        -webkit-transform: translate3d(0, 0, 0);
        transform: translate3d(0, 0, 0);
    }
}

